class MainApi {
  constructor({ orderUrl, salesUrl, headers, token }) {
    this._orderUrl = orderUrl;
    this._salesUrl = salesUrl;
    this._headers = headers;
    this._token = token;
  }

  getOrders(data) {
    // console.log(`${this._baseUrl}${data.values}T${data.time}.000Z&flag=0&key=${this._token}`, '000')
    return fetch(`${this._orderUrl}${data.valueOrder}T${data.time}.000Z&flag=1&key=${this._token}`, {
      method: "GET",
    }).then(res => res.ok ? res.json() : Promise.reject(new Error(`Ошибка ${res.status} : ${res.statusText}`)));
  }

  getSales(data) {
    // console.log(`${this._salesUrl}${data.values}T${data.time}.000Z&flag=0&key=${this._token}`, '000')
    return fetch(`${this._salesUrl}${data.valueSales}T${data.time}.000Z&flag=1&key=${this._token}`, {
      method: "GET",
    }).then(res => res.ok ? res.json() : Promise.reject(new Error(`Ошибка ${res.status} : ${res.statusText}`)));
  }

}

const config = {
  orderUrl: 'https://suppliers-stats.wildberries.ru/api/v1/supplier/orders?dateFrom=',
  salesUrl: 'https://suppliers-stats.wildberries.ru/api/v1/supplier/sales?dateFrom=',
  headers: { "Content-Type": "application/json" },
  token: 'MjJkNDg0ZTAtYjQ0YS00YzUzLWFlOTEtNWY3ZDRhYzM4YmNm',
};

const mainApi = new MainApi(config);
export default mainApi;